Vue.use(VueDragZone)
Vue.use(VueCodemirror)
Vue.use(VueQuillEditor)
Vue.use(VueTouchRipple)
Vue.use(VueVideoPlayer)
Vue.use(VueAwesomeSwiper)

Vue.material.theme.registerAll({
  blue: {
    primary: 'blue',
    accent: 'pink'
  }
});

// import Vonic from 'components/vonic'
// Vue.use(Vonic)

import App from 'projects/vue-awesome-swiper/App.vue'
// import App from 'projects/vue-touch-ripple/App.vue'
// import App from 'projects/vue-video-player/App.vue'
// import App from 'projects/vue-quill-editor/App.vue'
// import App from 'projects/vue-wang-editor/App.vue'
// import App from 'projects/vue-codemirror/App.vue'
// import App from 'projects/vue-touch-ripple/App.vue'
// import App from 'projects/vue-drag-zone/App.vue'

import 'sass/app.scss'

// ---------------------------------------------------------------------

// 配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [{
  name: 'index',
  path: '/',
  component: App
}]

const router = new VueRouter({
  routes,
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    return { y: 0 }
  }
})

const app = new Vue({
  router,
  render: h => h(App)
}).$mount('app')

